
*{
	margin:0;
	padding:0;
}
ul{list-style:none
}
.nav-list{
	position: fixed;
	left: 0;
	bottom: 0;
	top: 60px;
	line-height: 40px;
	background-color: #fff;
	border-right:1px solid rgba(16, 16, 16, 0.1);
	overflow-y: auto;
	z-index: 15;
	font-family: "Microsoft Yahei", sans-serif;
}
.nav-left{
	width:230px;
}
.nav-item{
    color: #4cae4c;
}
.nav-item a{
	font-size: 14px;
	color: #4cae4c;
	padding:0 50px;
	display: block;
	text-decoration: none;
}
.nav-item a:hover {
  color: #00cc66;
  background-color: #fafafa;
  cursor: pointer;
}
.nav-item a.router-link-active {
  color: #00cc66;
  background-color: #fafafa;
}



